/* 学习情况组件整体属性 */
.ClassLearing{
    width: 100%;
    height: 100%;
}

/* 左上侧框 */
.ClassLearning_two{
    width: 41%;
    height: 48%;
    margin: 1% 0.5% 1% 1%;
    border-radius: 3px;
    background-color: white;
    float: left;
    display: flex;
    flex-direction: column;
}
/* 课时及日期展示div, 其中 ">" 是子选择器，不会影响孙元素及以下层次的元素 */
.ClassLearning_two>div:nth-of-type(1){
    flex: 0 0 auto;
    width: 100%;
    height: 40px;
}
/* 日历图、雷达图 展示div */
.ClassLearning_two>div:nth-of-type(2){
    flex: 1 1 auto;
}

/* 课时选择器 */
.ClassLearning_two>div:nth-of-type(1)>div:nth-of-type(1){
    width: 33%;
    height: 100%;
    line-height: 40px;
    float: left;
}

.ClassLearning_two>div:nth-of-type(1)>div:nth-of-type(2){
    width: 55%;
    height: 100%;
    line-height: 20px;
    /* text-align: center; */
    float: right;
}

/* 日历图 */
.ClassLearning_Calendar{
    width: 50%;
    height: 100%;
    float: left;
}

/* 雷达图 */
.ClassLearning_Radar{
    width: 50%;
    height: 100%;
    float: left;
}

/* 词云图 */
.ClassLearning_WordCloud{
    width: 20%;
    height: 48%;
    margin: 1% 0.5% 1% 0.5%;
    border-radius: 3px;
    background-color: white;
    float: left;
}

/* 树状图 */
.ClassLearning_Tree{
    width: 35%;
    height: 48%;
    margin: 1% 1% 1% 0.5%;
    border-radius: 3px;
    background-color: white;
    float: left;
}

/* 平行坐标系图 */
.ClassLearning_Line{
    width: 62%;
    height: 47%;
    margin: 0 0.5% 1% 1%;
    border-radius: 3px;
    background-color: white;
    float: left;
}

/* 课程推荐 */
.ClassLearning_Suggest{
    width: 35%;
    height: 47%;
    margin: 0 1% 1% 0.5%;
    border-radius: 3px;
    background-color: white;
    float: left;
}