/* 日历图css样式 */

.ClassLearning_Calendar{
    display: flex;
    flex-direction: column;
}

/* 标题 */
.ClassLearning_Calendar>div:nth-of-type(1){
    width: 100%;
    height: 30px;
    flex: 0 0 auto;
    text-align: center;
    line-height: 30px;
    /* color: rgb(150, 150, 150); */
    font-size: 12px;
}

/* 绘图部分 */
.ClassLearning_Calendar>div:nth-of-type(2){
    flex: 1 1 auto;
}
.ClassLearning_Calendar>div:nth-of-type(2) svg{
    width: 100%;
    height: 100%;
}